// -----------------------------------------------------------------------------
// This file contains all styles related to the footer of the site
// -----------------------------------------------------------------------------
.footer {
	font-family: $inter-font-family;
	padding: 15px 0;
	text-align: center;
	color: $white;
	background: $black;
	
	@include media ('>=desktop') {
		padding: 27px 0;
	}
	
	.row {
		display: flex;
		flex-wrap: wrap;
		align-items: flex-end;
		justify-content: center;
		
		@include media ('>=tablet') {
			margin: 0;
			align-items: center;
			justify-content: space-between;
		}
	}
	
	.logo {
		margin: 0 0 15px;
		
		@include media ('>=tablet') {
			margin: 0 15px 0 0;
		}
	}
	
	.social-networks,
	.copy {
		width: 100%;
		
		@include media ('>=tablet') {
			width: auto;
		}
	}
	
	.social-networks {
		@include media ('>=tablet') {
			padding-left: 15px;
			margin: 0 -5px;
		}
	}
}

.social-networks {
	@extend %listreset;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	
	li {
		padding: 5px;
	}
	
	a {
		@include transition(color background);
		display: flex;
		align-items: center;
		justify-content: center;
		width: 30px;
		height: 30px;
		font-size: 14px;
		padding: 2px;
		color: $black;
		background: rgba($white, 0.5);
		border-radius: 50%;
		
		&:hover {
			color: $white;
			background: $gold;
		}
	}
}

.copy {
	font-size: 12px;
	font-weight: 300;
	margin-bottom: 10px;

	@include media ('>=tablet') {
		padding: 0 15px;
		margin: 0;
	}
}