// -----------------------------------------------------------------------------
// This file contains all styles related to the header of the site
// -----------------------------------------------------------------------------
.header {
	padding: 15px 0;
	background: $black;
	position: relative;

	@include media ('>=tablet') {
		padding: 20px 0;
	}

	.container {
		@include media ('>=tablet') {
			position: relative;
		}
	}

	.row {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
		margin: 0;
	}

	.logo {
		@include media ('>=tablet') {
			position: absolute;
			top: 50%;
			left: 15px;
			transform: translateY(-50%);
		}
	}
}

.nav-opener {
	display: block;
	width: 30px;
	height: 28px;
	background: transparent;
	border: none;
	overflow: hidden;
	position: relative;
	cursor: pointer;

	@include media ('>=tablet') {
		display: none;
	}

	&::before,
	&::after,
	span {
		@include transition(all);
		height: 2px;
		position: absolute;
		top: 14px;
		left: 0;
		right: 0;
		background: $white;
	}

	&::before,
	&::after {
		content: "";
		top: 6px;
	}

	&::after {
		top: 22px;
	}

	.nav-active & {

		&::after,
		&::before {
			top: 14px;
			transform: rotate(45deg);
		}

		&::after {
			transform: rotate(-45deg);
		}

		span {
			opacity: 0;
		}
	}
}

.drop {
	@include transition(opacity visibility);
	@include media ('<tablet') {
		display: flex;
		align-items: center;
		justify-content: center;
		height: calc(100vh - 58px);
		text-align: center;
		background: $black;
		overflow-y: auto;
		position: absolute;
		top: 100%;
		left: 0;
		right: 0;
		padding: 20px;
		z-index: 1;
		opacity: 0;
		visibility: hidden;
	}

	@include media ('>=tablet') {
		width: 100%;
		text-align: center;
		padding: 0 150px;
	}

	.nav-active & {
		opacity: 1;
		visibility: visible;
	}
}

.menu {
	@extend %listreset;
	font-family: $inter-font-family;
	font-size: 18px;

	@include media ('>=tablet') {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
		font-size: 12px;
		margin: 0 auto;
	}

	li {
		margin-bottom: 30px;

		@include media ('>=tablet') {
			padding: 3px 30px 0;
			margin: 0;
		}

		&.active {
			a {
				color: $gold;

				&::before {
					width: 100%;
				}
			}
		}
	}

	a {
		color: $white;
		padding-bottom: 8px;
		position: relative;

		&.active,
		&:hover {
			color: $gold;

			&::before {
				width: 100%;
			}
		}

		&::before {
			@include transition(width);
			content: "";
			height: 2px;
			position: absolute;
			width: 0;
			bottom: 0;
			left: 0;
			background: $gold;
		}
	}
}

.logo {
	display: block;
	width: 100px;

	a {
		display: block;
	}

	img {
		width: 100%;
	}
}