// stylelint-disable selector-no-qualifying-type, selector-max-compound-selectors

// -----------------------------------------------------------------------------
// This file contains all styles related to the form elements of the site
// -----------------------------------------------------------------------------

input[type='text'],
input[type='email'],
input[type='tel'],
input[type='search'],
textarea {
  @include transition(color background border);
  font-family: $inter-font-family;
  font-size: 12px;
  line-height: 1.571428;
  font-weight: 400;
  padding: 4px 8px;
  border: 1px solid $gray;
  background: $gray;
  margin-bottom: 14px;
}

input[type='text'],
input[type='email'],
input[type='tel'],
input[type='search'],
textarea {
  height: 40px;
  width: 100%;
  min-width: 0;
  -webkit-appearance: none;

  @include placeholder {
    color: $dark-gray;
    opacity: 1;
  }

  &.input-error {
    border-color: red;
  }

  &:focus {
    border-color: $gold;
    outline: none;
  }
}

textarea {
  height: 120px;
  resize: none;
}

input[type="button"] {
  @include transition(color background border);
  display: inline-block;
  vertical-align: middle;
  font-weight: 500;
  text-align: center;
  text-transform: capitalize;
  cursor: pointer;

  &.active,
  &:focus,
  &:hover {
    color: $white;
    background: $gold;
    border: 1px solid $gold;
    outline: none;
  }
}

input[type='search'] {
  border: 1px solid $gold;
}