// -----------------------------------------------------------------------------
// This file contains all styles related to the common layout of the site
// -----------------------------------------------------------------------------

.resize-active {
	* {
		transition: none !important;
	}
}

body {
	min-width: 360px;
	background-color: $body-bg;
	
	&.nav-active {
		@include media ('<tablet') {
			overflow: hidden;
		}
	}
}

.wrapper {
	min-height: 100vh;
	width: 100%;
	display: flex;
	flex-direction: column;
	position: relative;
	overflow: hidden;
	
	>.error-message {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}
}

.page-holder {
	flex: 1;
}

.container {
	max-width: $container-max-width;
	padding: 0 $gutter;
	margin: 0 auto;
}

img {
	max-width: 100%;
	height: auto;
	vertical-align: middle;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
	display: block;
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
	font-family: $oswald-font-family;
	font-weight: 700;
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
	margin-top: 0;
}

h1, .h1,
h2, .h2 {
	text-transform: capitalize;
	
	&.decor {
		position: relative;
		padding-bottom: 15px;
		margin-bottom: 20px;
		
		&::before {
			content: "";
			width: 50px;
			height: 4px;
			position: absolute;
			left: 0;
			bottom: 0;
			background: $gold;
		}
	}
}

h1, .h1 {
	font-size: 32px;
	
	@include media ('>=tablet') {
		font-size: 40px;
	}
}

h2, .h2 {
	font-size: 20px;
}

[type="submit"].btn,
.btn {
	@include transition(color background border-color);
	display: inline-block;
	vertical-align: middle;
	min-width: 116px;
	font-family: $oswald-font-family;
	font-size: 12px;
	line-height: 1.5;
	letter-spacing: 2px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center;
	padding: 10px 15px;
	color: $white;
	background: $gold;
	border: 1px solid $gold;
	cursor: pointer;
	
	&.btn-outline-light {
		color: $gold;
		background: transparent;
		border-color: $gold;
		
		&:hover {
			color: $white;
			background: $gold;
		}
	}
	
	&:hover {
		color: $white;
		background: darken($gold, 25%);
		border-color: darken($gold, 25%);
	}
}

.row {
	@include media ('>=tablet') {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -15px;
	}
	
	.col {
		@include media ('>=tablet') {
			width: 33.33%;
			padding: 0 15px;
		}
	}
}

.bg-img {
	background-size: cover;
	background-position: 50% 50%;
	background-repeat: no-repeat;
}

.text-center {
	text-align: center;
}

.hide {
	display: none !important;
}

.error-message,
.error {
	display: block;
	color: red;
}

.error-message {
	font-size: 12px;
	margin: -10px 0 10px;
}

.error {
	padding: 50px 0;
	text-align: center;
}

.intro {
	min-height: 260px;
	display: flex;
	align-items: center;
	padding: 50px 0;
	color: $white;
	text-align: center;
	
	&.intro-lg {
		text-align: left;
		padding: 50px 0 65px;
		
		@include media ('>=tablet') {
			min-height: 440px;
		}
	}
	
	.container {
		width: 100%;
	}
	
	.text-block {
		margin-bottom: 30px;
	}
	
	h1 {
		margin-bottom: 10px;
		
		&:only-child {
			margin: 0;
		}
	}
	
	p {
		margin: 0;
	}
}

.about,
.contact,
.error-section {
	padding: 50px 0;
	
	@include media ('>=desktop') {
		padding: 100px 0 80px;
	}
	
	.img-block,
	.text-block {
		@include media ('>=tablet') {
			width: 50%;
			padding: 0 15px;
		}
	}
	
	.img-block {
		margin-bottom: 30px;
		text-align: center;
		
		@include media ('>=tablet') {
			margin: 0;
			text-align: left;
		}
	}
}

.products {
	padding: 18px 0 40px;
	
	@include media ('>=desktop') {
		padding: 18px 0 40px;
	}
	
	.heading-block {
		margin-bottom: 50px;
		
		.row {
			@include media ('>=tablet') {
				align-items: center;
				justify-content: space-between;
			}
		}

		h1,
		.filter-block {
			@include media ('>=tablet') {
				padding: 0 15px;
			}
		}
		
		h1 {
			@include media ('>=tablet') {
				width: 100%;
			}

			@include media ('>=desktop') {
				max-width: 30%;
				margin: 0;
			}
		}

		.filter-block {
			@include media ('>=tablet') {
				width: 100%;
			}

			@include media ('>=desktop') {
				max-width: 70%;
			}
		}
	}
	
	h1 {
		margin-bottom: 30px;
		
		@include media ('>=tablet') {
			margin-bottom: 38px;
		}
	}
}

.products-holder {
	.row {
		@include media ('>=phone') {
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start;
			margin: 0 -15px;
		}
		
		@include media ('>=desktop') {
			margin: 0 -20px;
		}
	}
	
	.col {
		margin-bottom: 15px;
		
		@include media ('>=phone') {
			width: 50%;
			display: flex;
			padding: 0 15px;
		}
		
		@include media ('>=desktop') {
			width: 25%;
			padding: 0 20px;
			margin-bottom: 40px;
		}
	}
}

.product-card {
	@include transition(color);
	display: block;
	color: $black;
	border-radius: 8px;
	
	@include media ('>=phone') {
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
	}
	
	&:hover {
		color: $gold;
		
		.card-img {
			background: rgba($gold, 0.2);
			
			img {
				transform: scale(1.1);
			}
		}
	}
	
	.card-img {
		@include transition(background);
		margin-bottom: 22px;
		background: $gray;
		position: relative;
		overflow: hidden;
		
		&::before {
			content: "";
			display: block;
			padding-bottom: 110%;
		}
		
		img {
			@include transition(transform);
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			object-fit: cover;
		}
	}
	
	h2 {
		font-size: 20px;
		margin-bottom: 6px;
	}
	
	.category,
	.price {
		display: block;
	}
	
	.category {
		text-transform: capitalize;
		margin-bottom: 3px;
	}
	
	.price {
		font-weight: 900;
	}
}

.filter-form {
	
	.row {
		@include media ('>=tablet') {
			margin: 0;
		}
	}
}

.category-block {
	@include media ('>=tablet') {
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: center;
		padding: 0 15px 0 0;
	}
}

.search-block {
	position: relative;
	
	@include media ('>=tablet') {
		max-width: 240px;
		width: 100%;
	}
	
	i {
		position: absolute;
		left: 6px;
		top: 7px;
		font-size: 16px;
		color: $dark-gray;
	}
	
	input[type="search"] {
		height: 30px;
		background: transparent;
		padding: 4px 15px 4px 30px;
		margin: 0;
	}
}

.category-group {
	margin: 0 -5px 15px;
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	@include media ('>=tablet') {
		margin: 0 -10px;
	}
	
	input[type="button"] {
		color: $dark-gray;
		padding: 0 0 5px;
		margin: 5px 10px;
		background: transparent;
		border: none;
		border-bottom: 2px solid transparent;
		

		&:hover,
		&.active {
			color: $gold;
			border-bottom: 2px solid $gold;
		}
	}
}

.product-detail {
	padding: 50px 0;
	
	@include media ('>=desktop') {
		padding: 70px 0;
	}
	
	.container {
		&.container-md {
			max-width: 905px;
		}
	}
}

.product-info-list {
	@extend %listreset;
	
	li {
		margin-bottom: 17px;
	}
	
	.price {
		display: inline-block;
		vertical-align: baseline;
		font-size: 24px;
		margin-left: 5px;
		
		&:only-child {
			margin-left: 0;
		}
	}
}

.contact {
	.text-block {
		@include media ('>=desktop') {
			width: 38.7%;
		}
	}

	h1 {
		@include media ('>=tablet') {
			margin-bottom: 8px;
		}
	}

	p {
		@include media ('>=tablet') {
			margin-bottom: 30px;
		}
	}
}

.contact-form {
	.btn-holder {
		text-align: right;
		margin-top: 13px;

		.btn {
			min-width: 156px;
		}
	}
}

.error-section {
	padding: 100px 0;

	.row {
		@include media ('>=tablet') {
			align-items: center;
		}
	}

	h1 {
		@include media ('>=tablet') {
			margin-bottom: 15px;
		}
	}

	p {
		@include media ('>=tablet') {
			margin-bottom: 30px;
		}
	}
}